import storage from "good-storage";
// import { useStore } from "vuex";

export function saveUid(user) {
  const { uid } = user;
  storage.set("uId", uid);
  const item = storage.get("uId");
  return item;
}
export function saveUserInfo(user) {
  // const store = useStore();
  const {
    username,
    uid,
    avatar,
    status,
    resideprovince,
    residecity,
    residedist
  } = user;
  const location =
    resideprovince +
    (residecity ? "," + residecity : "") +
    (residedist ? "," + residedist : "");
  // console.log("store", store);
  // store.commit("setUserInfo", { username, uid, avatar, status, location });
  // store.commit("setUserInfoLocation", location);
  return { username, uid, avatar, status, location };
}
